import React from 'react';
import PropTypes from 'prop-types';
import ReactGA from 'react-ga'; // Importamos ReactGA para registrar eventos
import './MyButton.css'; // Importa los estilos CSS

function MyButton({ SvgIcon, svgString, text, onClick, styles, textAnalytics }) {

  const handleClick = (e) => {
    ReactGA.event({
      category: 'Button',
      action: 'Clicked MyButton',
      label: textAnalytics,
    });

    
    if (onClick) {
      onClick(e);
    }
  };

  return (
    <button
      style={styles}
      onClick={handleClick}
      className="boton-elegante"
    >
      {SvgIcon && <SvgIcon style={{ marginRight: '8px', width: '24px', height: '24px' }} />}

      {!SvgIcon && svgString && (
        <span
          style={{ marginRight: '8px', width: '24px', height: '24px' }}
          dangerouslySetInnerHTML={{ __html: svgString }}
        />
      )}

      <span>{text}</span>
    </button>
  );
}

MyButton.propTypes = {
  SvgIcon: PropTypes.elementType,    
  svgString: PropTypes.string,       
  text: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
  styles: PropTypes.object,
  textAnalytics: PropTypes.string.isRequired, 
};

export default MyButton;
