import React from 'react';
import { useThemeContext } from './Components/ThemeContext/useThemeContext';
import PagePortfolio from './PagePortfolio/PagePortfolio';
import PageProyecto from './PageProyecto/PageProyecto';
import { useApp } from './useApp';
import Header from './Header/Header';
function App() {
  const { currentPage, idProyecto, goToPortfolio, goToProyecto } = useThemeContext();
  const {  dataPortfolio, cargando} = useApp();

  return (
    <div className='w-100'>
      {!cargando && <Header />}
      {currentPage === 'portfolio' ? (
        <PagePortfolio cargando={cargando} dataPortfolio={dataPortfolio} goToProyecto={goToProyecto} />
      ) : (
        <div className='d-flex justify-content-center'>
        <PageProyecto id={idProyecto} goToPortfolio={goToPortfolio} />
        </div>
      )}
    </div>
  );
}

export default App;
