import React from 'react';
import PropTypes from 'prop-types';
import ReactGA from 'react-ga';
import './DownloadButton.css';

function DownloadButton({ text, fileUrl, SvgIcon, textAnalytics }) {


    const handleDownload = () => {
        ReactGA.event({
            category: 'Downloads',
            action: 'Clicked Download Button',
            label: textAnalytics, 
        });
    };

    return (
        <a href={fileUrl} download style={{ textDecoration: 'none' }} onClick={handleDownload}>
            <button className="button">
                <svg fill="currentColor" viewBox="0 0 24 24" className="icon">
                    {SvgIcon && <SvgIcon style={{ marginRight: '8px', width: '24px', height: '24px' }} />}
                </svg>
                {text} 
            </button>
        </a>
    );
}

DownloadButton.propTypes = {
    text: PropTypes.string.isRequired,
    fileUrl: PropTypes.string.isRequired,
    SvgIcon: PropTypes.elementType, 
    textAnalytics: PropTypes.string.isRequired,
};

export default DownloadButton;
