import React, { useState, useEffect, useCallback } from 'react';
import './Header.css';
import { useThemeContext } from '../Components/ThemeContext/useThemeContext';

function Header() {
  const { blackTheme, cambiarTheme, goToPortfolio } = useThemeContext();
  const [activeLink, setActiveLink] = useState('presentacion');
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const handleLinkClick = (link) => {
    const currentPath = window.location.pathname;

    const scrollToSection = (sectionId) => {
      setActiveLink(sectionId);
      const targetSection = document.querySelector(`#${sectionId}`);
      if (targetSection) {
        targetSection.scrollIntoView({ behavior: 'smooth' });
      }
      window.history.pushState({}, '', `#${sectionId}`);
    };

    if (currentPath.startsWith('/proyecto/')) {
      goToPortfolio();
      
        window.history.pushState({}, '', '/');
        scrollToSection(link);
    } else {
      scrollToSection(link);
    }
  };

  const handleScroll = useCallback(() => {
    const sections = document.querySelectorAll('section');
    let currentSection = '';
    const middleOfWindow = window.innerHeight / 2;

    if (window.scrollY === 0) {
      currentSection = 'presentacion';
    } else {
      sections.forEach((section) => {
        const sectionTop = section.getBoundingClientRect().top;
        const sectionHeight = section.offsetHeight;
        if (sectionTop <= middleOfWindow && sectionTop + sectionHeight > middleOfWindow) {
          currentSection = section.getAttribute('id');
        }
      });
    }

    if (currentSection && currentSection !== activeLink) {
      setActiveLink(currentSection);
    }
  }, [activeLink]);

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [handleScroll]);

  return (
    <header className='header'>
      <div className="web">
        <nav>
          <a
            href="#presentacion"
            className={activeLink === 'presentacion' ? 'active' : ''}
            onClick={() => handleLinkClick('presentacion')}
          >
            Presentación
          </a>
          <a
            href="#experiencia"
            className={activeLink === 'experiencia' ? 'active' : ''}
            onClick={() => handleLinkClick('experiencia')}
          >
            Experiencia
          </a>
          <a
            href="#proyectos"
            className={activeLink === 'proyectos' ? 'active' : ''}
            onClick={() => handleLinkClick('proyectos')}
          >
            Proyectos
          </a>
          <a
            href="#sobre-mi"
            className={activeLink === 'sobre-mi' ? 'active' : ''}
            onClick={() => handleLinkClick('sobre-mi')}
          >
            Sobre mí
          </a>
          <a
            href="#tecnologias"
            className={activeLink === 'tecnologias' ? 'active' : ''}
            onClick={() => handleLinkClick('tecnologias')}
          >
            Tecnologías
          </a>
          <button className='btn-change-theme' onClick={cambiarTheme}>
            {!blackTheme ? (
              <svg
                id="light"
                className="theme-toggle-icon size-5 transition-all"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                strokeWidth="2"
                stroke="currentColor"
                fill="none"
                strokeLinecap="round"
                strokeLinejoin="round"
                style={{ color: 'white', transform: 'scale(1)' }}
              >
                <path stroke="none" d="M0 0h24v24H0z" fill="none"></path>
                <path d="M12 12m-4 0a4 4 0 1 0 8 0a4 4 0 1 0 -8 0"></path>
                <path d="M3 12h1m8 -9v1m8 8h1m-9 8v1m-6.4 -15.4l.7 .7m12.1 -.7l-.7 .7m0 11.4l.7 .7m-12.1 -.7l-.7 .7"></path>
              </svg>
            ) : (
              <svg
                id="dark"
                className="theme-toggle-icon absolute size-5 transition-all"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                strokeWidth="2"
                stroke="currentColor"
                fill="none"
                strokeLinecap="round"
                strokeLinejoin="round"
                style={{ color: 'white', transform: 'scale(1)' }}
              >
                <path stroke="none" d="M0 0h24v24H0z" fill="none"></path>
                <path d="M12 3c.132 0 .263 0 .393 0a7.5 7.5 0 0 0 7.92 12.446a9 9 0 1 1 -8.313 -12.454z"></path>
              </svg>
            )}
          </button>
        </nav>
      </div>

      <div className="mobile">
        <button
          className={`hamburger ${isMenuOpen ? 'open' : ''}`}
          onClick={() => setIsMenuOpen(!isMenuOpen)}
        >
          <div className="bar"></div>
          <div className="bar"></div>
          <div className="bar"></div>
        </button>

        <nav className={isMenuOpen ? 'open' : ''}>
          <a href="#presentacion" className={activeLink === 'presentacion' ? 'active' : ''} onClick={() => handleLinkClick('presentacion')}>
            Presentación
          </a>
          <a href="#experiencia" className={activeLink === 'experiencia' ? 'active' : ''} onClick={() => handleLinkClick('experiencia')}>
            Experiencia
          </a>
          <a href="#proyectos" className={activeLink === 'proyectos' ? 'active' : ''} onClick={() => handleLinkClick('proyectos')}>
            Proyectos
          </a>
          <a href="#sobre-mi" className={activeLink === 'sobre-mi' ? 'active' : ''} onClick={() => handleLinkClick('sobre-mi')}>
            Sobre mí
          </a>
          <a href="#tecnologias" className={activeLink === 'tecnologias' ? 'active' : ''} onClick={() => handleLinkClick('tecnologias')}>
            Tecnologías
          </a>


          <div className='w-100 d-flex justify-content-center'>
            <label className="switch">
              <span className="sun"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><g fill="#ffd43b"><circle r="5" cy="12" cx="12"></circle><path d="m21 13h-1a1 1 0 0 1 0-2h1a1 1 0 0 1 0 2zm-17 0h-1a1 1 0 0 1 0-2h1a1 1 0 0 1 0 2zm13.66-5.66a1 1 0 0 1 -.66-.29 1 1 0 0 1 0-1.41l.71-.71a1 1 0 1 1 1.41 1.41l-.71.71a1 1 0 0 1 -.75.29zm-12.02 12.02a1 1 0 0 1 -.71-.29 1 1 0 0 1 0-1.41l.71-.66a1 1 0 0 1 1.41 1.41l-.71.71a1 1 0 0 1 -.7.24zm6.36-14.36a1 1 0 0 1 -1-1v-1a1 1 0 0 1 2 0v1a1 1 0 0 1 -1 1zm0 17a1 1 0 0 1 -1-1v-1a1 1 0 0 1 2 0v1a1 1 0 0 1 -1 1zm-5.66-14.66a1 1 0 0 1 -.7-.29l-.71-.71a1 1 0 0 1 1.41-1.41l.71.71a1 1 0 0 1 0 1.41 1 1 0 0 1 -.71.29zm12.02 12.02a1 1 0 0 1 -.7-.29l-.66-.71a1 1 0 0 1 1.36-1.36l.71.71a1 1 0 0 1 0 1.41 1 1 0 0 1 -.71.24z"></path></g></svg></span>
              <span className="moon"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512"><path d="m223.5 32c-123.5 0-223.5 100.3-223.5 224s100 224 223.5 224c60.6 0 115.5-24.2 155.8-63.4 5-4.9 6.3-12.5 3.1-18.7s-10.1-9.7-17-8.5c-9.8 1.7-19.8 2.6-30.1 2.6-96.9 0-175.5-78.8-175.5-176 0-65.8 36-123.1 89.3-153.3 6.1-3.5 9.2-10.5 7.7-17.3s-7.3-11.9-14.3-12.5c-6.3-.5-12.6-.8-19-.8z"></path></svg></span>
              <input onClick={cambiarTheme} type="checkbox" className="input" />
              <span className="slider"></span>
            </label>
          </div>

        </nav>
      </div>

    </header>
  );
}

export default Header;
