import { useState, useEffect, useCallback } from 'react';
import FireBaseConexion from './Services/FireBase';


const fireBaseConexion = new FireBaseConexion();

export function useApp() {

    const [dataPortfolio, setDataPortfolio] = useState()
    const [cargando, setCargando] = useState(true)


    const todosLosFetch = useCallback(async () => {
        try {
            let arrayObjPortfolio = {};
            const tecnologias = await fireBaseConexion.getFireStore('Tecnologias');
            const carrera = await fireBaseConexion.getFireStore('Carrera');
            const proyectos = await fireBaseConexion.getFireStore('Proyectos');
            arrayObjPortfolio.tecnologias = tecnologias;
            arrayObjPortfolio.carrera = carrera;
            arrayObjPortfolio.proyectos = proyectos;
            setDataPortfolio(arrayObjPortfolio);
            console.log(arrayObjPortfolio);
        } catch (err) {
            console.error("Error en todosLosFetch:", err);  // Añade este log
        } finally {
            setCargando(false)
        }
    }, []);


    useEffect(() => {
        todosLosFetch();

    }, [todosLosFetch])


    return {
        dataPortfolio,
        cargando
    };
}

export default useApp;
