import React from 'react';
import MyButton from '../../../Components/MyButton';
import { LinkedIn, GitHub, MailOutline } from '@mui/icons-material';
import './Presentacion.css';

function Presentacion() {
  const startDate = new Date('2022-08-15');
  const currentDate = new Date();
  const yearsOfExperience = currentDate.getFullYear() - startDate.getFullYear();

  const hasNotAnniversaryYet =
    currentDate.getMonth() < startDate.getMonth() ||
    (currentDate.getMonth() === startDate.getMonth() && currentDate.getDate() < startDate.getDate());

  const experience = hasNotAnniversaryYet ? yearsOfExperience - 1 : yearsOfExperience;

  return (
    <>
      <h1 className="welcome-message">
        ¡Bienvenido a mi Portfolio!
      </h1>
      <p className="text-center">
        Soy Joaco, un <strong className='highlight'>Desarrollador de Software Full Stack</strong> con más de {experience} años de experiencia.<br />
      </p>


      <div className="button-container">
        <MyButton
          text="LinkedIn"
          SvgIcon={LinkedIn}
          onClick={() => window.open('https://www.linkedin.com/in/joaquin-burgos-75186123b/', '_blank')}
          textAnalytics={'LinkedIn'}

        />

        {/* <MyButton
          text="Github"
          SvgIcon={GitHub} // Icono de Github
          onClick={()=>alert('hiciste click')}
        /> */}
        <MyButton
          text="Contáctame"
          SvgIcon={MailOutline}
          onClick={() => window.location.href = 'mailto:joaquinburgos917@gmail.com'}
          textAnalytics={'Contáctame'}

        />

      </div>
    </>
  );
}

export default Presentacion;
