import React, { useRef } from 'react';
import './Tecnologias.css'; // Asegúrate de que este archivo CSS está bien enlazado

function Tecnologias(props) {
    const { dataTecnologias } = props;
    const cardRefs = useRef([]);

    // Agrupar tecnologías por tipo
    const groupedTecnologias = dataTecnologias.reduce((acc, tecnologia) => {
        const tipo = tecnologia.Tipo || 'Otros';
        if (!acc[tipo]) {
            acc[tipo] = [];
        }
        acc[tipo].push(tecnologia);
        return acc;
    }, {});

    // Ordenar las claves (tipos) alfabéticamente
    const sortedTipos = Object.keys(groupedTecnologias).sort();

    // Mostrar las tarjetas
    const displayedCards = sortedTipos.map((tipo, index) => (
        <div
            className='container noselect'
            ref={el => (cardRefs.current[index] = el)}
            key={index}
        >
            <div className="canvas">
                <div className="tracker tr-1"></div>
                <div className="tracker tr-2"></div>
                <div className="tracker tr-3"></div>
                <div className="tracker tr-4"></div>
                <div className="tracker tr-5"></div>
                <div className="tracker tr-6"></div>
                <div className="tracker tr-7"></div>
                <div className="tracker tr-8"></div>
                <div className="tracker tr-9"></div>
                <div className="tracker tr-10"></div>
                <div className="tracker tr-11"></div>
                <div className="tracker tr-12"></div>
                <div className="tracker tr-13"></div>
                <div className="tracker tr-14"></div>
                <div className="tracker tr-15"></div>
                <div className="tracker tr-16"></div>
                <div className="tracker tr-17"></div>
                <div className="tracker tr-18"></div>
                <div className="tracker tr-19"></div>
                <div className="tracker tr-20"></div>
                <div className="tracker tr-21"></div>
                <div className="tracker tr-22"></div>
                <div className="tracker tr-23"></div>
                <div className="tracker tr-24"></div>
                <div className="tracker tr-25"></div>
                <div id="card">
                    <h3>{tipo}</h3>
                    <div className="tecnologia-images"> {/* Contenedor para las imágenes */}
                        {groupedTecnologias[tipo].map((tecnologia) => (
                            <div className='d-flex justify-content-center align-items-center flex-column' key={tecnologia.id}>
                                <div style={{ height: '60px' }}>
                                    <img src={tecnologia.urlImg[0]} alt={tecnologia.nombreTecnologia} className='tecnologia-image' />
                                </div>
                                <h4 className="tecnologia-name">{tecnologia.nombreTecnologia}</h4> {/* Añadido nombre aquí */}
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    ));

    return (
        <div className='container-section'>
            <h2>Tecnologías</h2>
            <div className='tecnologias-container'>
                {displayedCards}
            </div>
        </div>
    );
}

export default Tecnologias;
