import { useState, useEffect, useCallback } from 'react';
import FireBaseConexion from '../Services/FireBase';
import { useThemeContext } from '../Components/ThemeContext/useThemeContext';

const fireBaseConexion = new FireBaseConexion();

export function usePageProyecto(idProyecto) {
  const [dataProyecto, setDataProyecto] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const { goToPortfolio } = useThemeContext();


  useEffect(() => {
    const handlePopState = (event) => {
      goToPortfolio();
    };

    // Añade el listener para el evento popstate
    window.addEventListener('popstate', handlePopState);

    return () => {
      window.removeEventListener('popstate', handlePopState);
    };
  }, [goToPortfolio]);

  const fetchProyecto = useCallback(async () => {
    try {
      setLoading(true); // Comienza la carga
      const proyecto = await fireBaseConexion.getDocument('Proyectos', idProyecto);
      setDataProyecto(proyecto);
    } catch (err) {
      console.error('Error fetching project:', err);
      setError(err);
    } finally {
      setLoading(false);
    }
  }, [idProyecto]);



  

  useEffect(() => {
    if (idProyecto) {
      fetchProyecto();
    }
  }, [idProyecto, fetchProyecto]);


  


  return {
    dataProyecto,
    loading,
    error,
  };
}

export default usePageProyecto;
