import React from 'react';
import './Proyectos.css';
import useProyectos from './useProyectos';
import { useThemeContext } from '../../../Components/ThemeContext/useThemeContext';

function Proyectos(props) {
  const {
    dataProyectos,
    visibleCount,
    loadMoreProjects,
    loadLessProjects
  } = useProyectos(props);

  const { goToProyecto } = useThemeContext();

  return (
    <div className='container-section' 
    style={{position: 'relative', width: '100vw', display:'flex',justifyContent: 'center',alignItems: 'center'}}>
      <h2>Proyectos</h2>
      <div className="grid-container">
        {dataProyectos
          ?.sort((a, b) => a.orden - b.orden)
          .slice(0, visibleCount) 
          .map((proyecto, index) => (
            <div 
              key={index} 
              className={`card-proyecto`}
              onClick={() => goToProyecto(proyecto.id)}
              style={{ cursor: 'pointer' }} 
            >
              <figure>
                <img src={proyecto.imgPresentacion} alt={`Proyecto ${index}`} />
              </figure>
              <figcaption className='presentacion-proyecto'>
                <div className='row-descripcion'>
                  <h1 className="highlight">{proyecto.titulo}</h1>
                  <p style={{ textAlign: 'left' }}>
                    {proyecto?.descripcionCorta}
                  </p>
                </div>
                <ul className="technologies-list d-flex">
                  {proyecto.tecnologias.map((tecnologia, techIndex) => (
                    <li key={techIndex}>{tecnologia}</li>
                  ))}
                </ul>
              </figcaption>
            </div>
          ))}
      </div>

      <div className="button-container">
      {((window.innerWidth < 480 && visibleCount > 2) || (window.innerWidth >= 480 && visibleCount > 4)) && (
          <button className="highlight btn-ver-menos" onClick={loadLessProjects}>
            Ver menos
          </button>
        )}
        {visibleCount < dataProyectos.length && (
          <button className="highlight btn-ver-todos" onClick={loadMoreProjects}>
            Ver más
          </button>
        )}
      </div>
    </div>
  );
}

export default Proyectos;
