import { useState} from 'react';

export function useProyectos(props) {
    const {dataProyectos}=props
    const [visibleCount, setVisibleCount] = useState(() => {
            return window.innerWidth < 480 ? 2 : 4;
    });

    
    const loadMoreProjects = () => {
        setVisibleCount(prevCount => Math.min(prevCount + 2, dataProyectos.length));
    };

    
    const loadLessProjects = () => {
        setVisibleCount(prevCount => {
            const minVisibleCount = window.innerWidth < 480 ? 2 : 4;
            return Math.max(prevCount - 2, minVisibleCount);
        });
    };



    return {
        dataProyectos,
        visibleCount,
        loadMoreProjects,
        loadLessProjects
    };
}

export default useProyectos;
